import React from 'react';
import PropTypes from 'prop-types';
import { Flex, Text } from 'rebass/styled-components';
import { graphql, StaticQuery } from 'gatsby';
import styled from 'styled-components';
import Fade from 'react-reveal/Fade';
import Section from '../components/Section';
import { Card, CardContainer } from '../components/Card';
import Triangle from '../components/Triangle';

const Background = () => (
  <div>
    <Triangle
      color="secondaryLight"
      height={['80vh', '80vh']}
      width={['100vw', '100vw']}
      invertX
    />

    <Triangle
      color="background"
      height={['50vh', '20vh']}
      width={['50vw', '50vw']}
      invertX
    />

    <Triangle
      color="primaryDark"
      height={['25vh', '40vh']}
      width={['75vw', '60vw']}
      invertX
      invertY
    />

    <Triangle
      color="backgroundDark"
      height={['25vh', '20vh']}
      width={['100vw', '100vw']}
      invertY
    />
  </div>
);

const CARD_HEIGHT = '100px';

const MEDIA_QUERY_SMALL = '@media (max-width: 400px)';

const Title = styled(Text)`
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  display: table;
  border-bottom: ${(props) => props.theme.colors.primary} 5px solid;
`;

const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 100%;
  width: calc(100% - ${CARD_HEIGHT});

  ${MEDIA_QUERY_SMALL} {
    width: calc(100% - (${CARD_HEIGHT} / 2));
  }
`;

const Testimonial = ({
                       name,
                       text,

                     }) => (
                       <Card p={0} style={{width:'100%'}}>
                         <Flex style={{ minHeight: CARD_HEIGHT, width:'100%' }}>
                           <TextContainer>
                             <Text width={[1]} style={{ overflow: 'auto' }} color="text">
                               <blockquote>
                                 <i>
                                   “ {text.text} “
                                 </i>

                               </blockquote>
                             </Text>
                             <div style={{display: 'flex',justifyContent: 'flex-end'}}>
                               <Title my={2} pb={1} color="text">
                                 {name}
                               </Title>
                             </div>
                           </TextContainer>
                         </Flex>
                       </Card>
);

Testimonial.propTypes = {
  name: PropTypes.string.isRequired,
  text: PropTypes.shape,
};

const Testimonials = () => (
  <Section.Container id="testimonials" Background={Background}>
    <Section.Header name="Testimonials" icon="'' " label="notebook" />
    <StaticQuery
      query={graphql`
        query TestimonialsQuery {
          allContentfulTestimonials {
            edges {
              node {
                name,
                text{
                  text
                }
              }
            }
          }
        }
      `}
      render={({ allContentfulTestimonials }) => (
        <CardContainer minWidth="100%" style={{width:'100%'}}>
          {console.log(allContentfulTestimonials)}
          {allContentfulTestimonials.edges.map((p, i) => (
            <div style={{width:'100%'}}>
              <Fade bottom delay={i * 200} key={p.node.id} style={{width:'100%'}}>
                <Testimonial {...p.node} />
              </Fade>
            </div>

          ))}
        </CardContainer>
      )}
    />
  </Section.Container>
);

export default Testimonials;
